.la-ball-clip-rotate-pulse,
.la-ball-clip-rotate-pulse > div {
  position: relative;
  box-sizing: border-box;
}
.la-ball-clip-rotate-pulse {
  display: block;
  font-size: 0;
  color: #fff;
}
.la-ball-clip-rotate-pulse.la-dark {
  color: #333;
}
.la-ball-clip-rotate-pulse > div {
  display: inline-block;
  float: none;
  background-color: currentColor;
  border: 0 solid currentColor;
}
.la-ball-clip-rotate-pulse {
  width: 32px;
  height: 32px;
}
.la-ball-clip-rotate-pulse > div {
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 100%;
}
.la-ball-clip-rotate-pulse > div:first-child {
  position: absolute;
  width: 32px;
  height: 32px;
  background: transparent;
  border-style: solid;
  border-width: 2px;
  border-right-color: transparent;
  border-left-color: transparent;
  -webkit-animation: ball-clip-rotate-pulse-rotate 1s
    cubic-bezier(0.09, 0.57, 0.49, 0.9) infinite;
  animation: ball-clip-rotate-pulse-rotate 1s
    cubic-bezier(0.09, 0.57, 0.49, 0.9) infinite;
}
.la-ball-clip-rotate-pulse > div:last-child {
  width: 16px;
  height: 16px;
  -webkit-animation: ball-clip-rotate-pulse-scale 1s
    cubic-bezier(0.09, 0.57, 0.49, 0.9) infinite;
  animation: ball-clip-rotate-pulse-scale 1s cubic-bezier(0.09, 0.57, 0.49, 0.9)
    infinite;
}
.la-ball-clip-rotate-pulse.la-sm {
  width: 16px;
  height: 16px;
}
.la-ball-clip-rotate-pulse.la-sm > div:first-child {
  width: 16px;
  height: 16px;
  border-width: 1px;
}
.la-ball-clip-rotate-pulse.la-sm > div:last-child {
  width: 8px;
  height: 8px;
}
.la-ball-clip-rotate-pulse.la-2x {
  width: 64px;
  height: 64px;
}
.la-ball-clip-rotate-pulse.la-2x > div:first-child {
  width: 64px;
  height: 64px;
  border-width: 4px;
}
.la-ball-clip-rotate-pulse.la-2x > div:last-child {
  width: 32px;
  height: 32px;
}
.la-ball-clip-rotate-pulse.la-3x {
  width: 96px;
  height: 96px;
}
.la-ball-clip-rotate-pulse.la-3x > div:first-child {
  width: 96px;
  height: 96px;
  border-width: 6px;
}
.la-ball-clip-rotate-pulse.la-3x > div:last-child {
  width: 48px;
  height: 48px;
}
/*
  * Animations
  */
@-webkit-keyframes ball-clip-rotate-pulse-rotate {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg);
  }
  50% {
    -webkit-transform: translate(-50%, -50%) rotate(180deg);
    transform: translate(-50%, -50%) rotate(180deg);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
@keyframes ball-clip-rotate-pulse-rotate {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg);
  }
  50% {
    -webkit-transform: translate(-50%, -50%) rotate(180deg);
    transform: translate(-50%, -50%) rotate(180deg);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
@-webkit-keyframes ball-clip-rotate-pulse-scale {
  0%,
  100% {
    opacity: 1;
    -webkit-transform: translate(-50%, -50%) scale(1);
    transform: translate(-50%, -50%) scale(1);
  }
  30% {
    opacity: 0.3;
    -webkit-transform: translate(-50%, -50%) scale(0.15);
    transform: translate(-50%, -50%) scale(0.15);
  }
}
@keyframes ball-clip-rotate-pulse-scale {
  0%,
  100% {
    opacity: 1;
    -webkit-transform: translate(-50%, -50%) scale(1);
    transform: translate(-50%, -50%) scale(1);
  }
  30% {
    opacity: 0.3;
    -webkit-transform: translate(-50%, -50%) scale(0.15);
    transform: translate(-50%, -50%) scale(0.15);
  }
}

$nunito-font: "Nunito", sans-serif;

// mixins
@mixin breakpoint($point) {
  @if $point==mobile {
    @media (max-width: 480px) and (min-width: 320px) {
      @content;
    }
  }
}

// keyrames
@keyframes floating {
  from {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  65% {
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
  }
  to {
    -webkit-transform: translateY(-0px);
            transform: translateY(-0px);
  }
}

html {
  height: 100%;
}

.mars {
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  height: 27vmin;
  background: url("https://assets.codepen.io/1538474/mars.svg") no-repeat bottom
    center;
  background-size: cover;
}

.logo-404 {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  top: 16vmin;
  width: 30vmin;

  @include breakpoint(mobile) {
    top: 45vmin;
  }
}

.meteor {
  position: absolute;
  right: 2vmin;
  top: 16vmin;
}

.title {
  color: rgb(124, 0, 124);
  font-family: $nunito-font;
  font-weight: 600;
  text-align: center;
  font-size: 5vmin;
  margin-top: 31vmin;

  @include breakpoint(mobile) {
    margin-top: 65vmin;
  }
}

.subtitle {
  color: rgb(9, 9, 9);
  font-family: $nunito-font;
  font-weight: 400;
  text-align: center;
  font-size: 3.5vmin;
  margin-top: -1vmin;
  margin-bottom: 9vmin;
}

.btn-back {
  border: 1px solid rgb(124, 0, 124);
  color: rgb(124, 0, 124);
  height: 5vmin;
  padding: 12px;
  font-family: $nunito-font;
  text-decoration: none;
  border-radius: 5px;

  &:hover {
    background: white;
    color: #4d007d;
  }

  @include breakpoint(mobile) {
    font-size: 3.5vmin;
  }
}

.astronaut {
  position: absolute;
  top: 18vmin;
  left: 10vmin;
  height: 30vmin;
  -webkit-animation: floating 3s infinite ease-in-out;
          animation: floating 3s infinite ease-in-out;

  @include breakpoint(mobile) {
    top: 2vmin;
  }
}

.spaceship {
  position: absolute;
  bottom: 15vmin;
  right: 24vmin;

  @include breakpoint(mobile) {
    width: 45vmin;
    bottom: 18vmin;
  }
}

